@import url('https://fonts.googleapis.com/css2?family=Just+Another+Hand&display=swap');


.root {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  width: 100%;
  height: 100vh;
  color: white;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100%;
  height: 100%; /* Fill the height of the page */
  background-size: cover;
  background-position: center;
  animation: changeBackground 15s infinite;
}

.box {
  display: flex;
  flex-direction: column; /* Stack items vertically */
  justify-content: center;
  align-items: center;
  width: 100%; /* Ensure cards take full width */
  gap: 2rem; /* Add spacing between the cards */
  z-index: 4;
  color: white;
}

.details {
  position: relative;
  background-color: rgba(0, 0, 0, 0.6); /* Semi-transparent background */
  padding: 2rem;
  width: 80%;
  max-width: 40rem; /* Limit card width */
  text-align: center;
  margin: 0 auto; /* Center the cards */
  border: 2px solid #d3d3d3; /* Card border */
  border-radius: 8px;
}

.details::before {
  content: ""; /* Removed "EVENTS" from before pseudo-element */
}

.title {
  font-size: 2rem;
  font-family: 'Just Another Hand', cursive;
  font-weight: bold;
  color: white;
  margin: 0;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
  margin-bottom: 1rem;
}

.description {
  font-size: 1rem;
  font-family: Arial, sans-serif;
  line-height: 1.5;
  font-weight: normal;
  color: #d3d3d3;
  margin: 1rem 0;
  padding: 1rem;
  border: none;
  border-radius: 8px;
  background-color: rgba(255, 255, 255, 0.1); /* Slightly lighter background for description */
}



@keyframes changeBackground {
  0% {
    background-image: url('../../public/eventImages/image1.jpeg');
  }
  20% {
    background-image: url('../../public/eventImages/image2.jpeg');
  }
  40% {
    background-image: url('../../public/eventImages/image3.jpeg');
  }
  60% {
    background-image: url('../../public/eventImages/image4.jpeg');
  }
  80% {
    background-image: url('../../public/eventImages/image5.jpeg');
  }
  100% {
    background-image: url('../../public/eventImages/image1.jpeg');
  }
}

@media (max-width: 768px) {
  .details {
    width: 90%;
    padding: 1rem;
  }

  .title {
    font-size: 1.5rem;
    margin-top: 1rem;
  }

  .description {
    font-size: 0.9rem;
    padding: 0.8rem;
  }
}
