/* Root Section */
.root {
    background-color: white;
    color: black;
    overflow: hidden;
    text-align: justify;
}

/* Backbox Section */
.backbox {
    background-color: #091235;
    margin-left: 2%;
    margin-right: 2%;
    padding: 1.5rem;
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-bottom: 2rem;
}

/* About Us Text */
.aboutUs {
    color: white;
    font-size: 1.8rem;
    font-weight: bolder;
    display: inline;
}

/* Dots Under "About Us" */
.dots {
    height: 20px;
    width: 250px;
    color: white;
    display: flex;
    justify-content: space-between;
}

.dot {
    background-color: white;
    height: 8px;
    width: 8px;
    border-radius: 50%;
    display: flex;
    align-items: center;
}

/* Quote Section */
.quote {
    color: white;
    font-weight: bold;
    font-style: italic;
    padding-bottom: 2rem;
}

/* Content Section */
.content {
    margin: 2%;
}

/* Container for Text and Image */
.container {
    display: flex;
    flex-direction: row;
    color: white;
    gap: 3rem;
}

@media(max-width: 1000px) {
    .container {
        flex-direction: column-reverse;
    }
}

/* Image */
.image {
    width: 35rem;
}

@media(max-width: 1000px) {
    .image {
        width: 90vw;
        height: 50vw;
    }
}

/* Line Divider */
.line1 {
    height: 2px;
    background-color: rgb(0, 0, 0); /* Ensure the same color for both lines */
    margin-top: 5px;
    margin-bottom: 5px;
}

/* Title Style */
.title {
    font-size: 1.8rem;
    color: black;
    text-transform: capitalize;
}

/* Where it All Began Section */
.where {
    margin-top: 2%;
    padding-bottom: 2%;
}

/* Q&A Section */
.qna {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2rem;
}

/* Question Style */
.question {
    font-size: 2.5rem;
}

@media(max-width: 300px) {
    .question {
        font-size: 1.5rem;
    }
}

/* Answer Style */
.answer {
    font-size: 4rem;
}

@media(max-width: 400px) {
    .answer {
        font-size: 3rem;
    }
}

@media(max-width: 300px) {
    .answer {
        font-size: 2rem;
    }
}
