.container {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  width: 100%; /* Allow the container to span full width */
  max-width: 600px; /* Limit container width for responsiveness */
  margin: 0 auto; /* Center the container */
}

.card {
  background-color: #555;
  color: white;
  padding: 1.5rem; /* Adjust padding to suit the smaller size */
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  position: relative;
  transform: translate(0.3rem, 0.3rem); /* Smaller translate effect */
  z-index: 1;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  width: 90%; /* Reduced card width */
  max-width: 400px; /* Further limit card width */
  margin: 0 auto; /* Center the card */
}

.card:hover {
  transform: translate(0, 0);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #242754;
  z-index: -1;
  transform: translate(-0.3rem, -0.3rem); /* Match the smaller translate effect */
  border-radius: 8px;
}
.card img {
  width:40%; /* Ensure the image scales within the card width */
  max-width: 300px; /* Limit the maximum width of the image */
  height: 40%; /* Maintain the aspect ratio */
  border-radius: 8px; /* Optional: match the card's rounded corners */
  margin: 0 auto; /* Center the image */
  display: block; 
}
