
.sliderContainer {
  width: 100%;
  height: 100vh;
  background-color: #f0f0f0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.sliderImages {
  display: flex;
  align-items: center;
  gap: 23px;
}

.sliderImg {
  width: 110px;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  transition: 0.7s ease;
}

.sliderImg img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 6px;
}

.sliderImg:first-child,
.sliderImg:last-child {
  height: 450px; 
}

.sliderImg:nth-child(2),
.sliderImg:nth-child(6) {
  height: 530px; 
}

.sliderImg:nth-child(3),
.sliderImg:nth-child(4),
.sliderImg:nth-child(5) {
  height: 635px; 
}

h1 {
  font-size: 40px;
  font-weight: 700;
  text-align: left;
  text-transform: uppercase;
  color: #fff;
  position: absolute;
  top: 50%;
  left: -10%;
  transform: rotate(270deg);
}

.sliderImg.active {
  width: 750px !important; 
  height: 720px !important;
}

.sliderImg.active h1 {
  display: none;
}
