.wrapper {
  width: 100%;
  background-color: white; /* Set the background to white */
  align-items: center;
  display: flex;
  flex-direction: column;
  color: black;
}

.head {
  margin-top: 15px;
  font-size: 30px;
  font-weight: bolder;
}

@media (max-width: 600px) {
  .head {
      font-size: 19px;
  }
}

.body {
  display: flex;
  width: 100%;
  padding: 1%;
  justify-content: space-between; /* This ensures the image and text are spaced out */
  align-items: center; /* Vertically center the items */
}

.left {
  width: 40vw; /* Adjust the width of the image container */
  margin-right: 2%;
}

.image {
  width: 100%; /* Make sure the image fills its container */
  height: auto;
  border-radius: 8px; /* Optional: adds rounded corners to the image */
}

.right {
  width: 55vw;
  text-align: justify;
  font-weight: 400;
  font-size: 16px;
}

@media (max-width: 1200px) {
  .right {
      width: 95vw;
  }
  .left {
      width: 95vw;
      margin-right: 0;
      margin-bottom: 20px; /* Adds space below the image on smaller screens */
  }
}

@media (max-width: 600px) {
  .right {
      font-size: 14px;
  }
  .image {
      width: 80%; /* Make the image smaller on small screens */
  }
}
